@use '../../shared.scss' as *;

.frame {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;

    &>svg:last-child {
        position: relative;
        z-index: -1;
    }

    &>*:first-child {
        max-width: 100%;
        max-height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
    }

    &.hidden>*:first-child  {
        display: none;
    }

    &:not(.hidden)>*:first-child  {
        :global {
            animation: fadein ease-in 0.3s;
        }
    }
}
